import React,{useState} from 'react'
import './login.css'
import {useDispatch} from "react-redux"
import {login} from "../features/userSlice"


const Login = () => {
    const [name,setName] = useState("");
    const [password,setPassword] = useState("");

    const dispatch = useDispatch();

   const handleSubmit = (e) => {
    //   console.log(name +" " + password);      
       e.preventDefault();
       if(name === ""  && password ===""){
           alert("Username and password must be filled");
           return false;
       }
       else if(password.length <= 6){
          alert("Password must have at least 6 character");
          return false;
       }

       if(name === "Employee_One@gmail.com" && password === "Employee123"){
          dispatch (
            login({
                 name : name,
                 passeword : password,
                 loggedIn :true      
            }) 
          )      
         }
         else {
            alert("Enter Valid Username and password");
         }
   }    

    return (
        <div className="login">
            {/* <pre>{JSON.stringify([name,password],undefined,2)}</pre> */}
            <form className="login_form" onSubmit={(e)=> handleSubmit(e)}> 
                <h1>Login here </h1>
                <input type="email" onChange={(e)=> setName(e.target.value)} placeholder="Enter your Username" value={name}/>
                <input type="password" onChange={(e)=> setPassword(e.target.value)} placeholder="Enter your Password" value={password}/>
               <button type="submit" className="submit_btn">Login</button>
              
            </form>
        </div>
    )
}

export default Login
