import './App.css';
import Login from './Components/Login';
import {useSelector} from "react-redux"
import { selectUser } from './features/userSlice';
import Reset from './Components/Reset';


function App() {
 const user = useSelector(selectUser);
 

  return (
    <div className="App">
      {user ? <Reset/> : <Login/>}
    </div>
  );
}

export default App;
